import { Component } from '@angular/core';
import { NavController, IonicPage, NavParams } from 'ionic-angular';
import { SettingProvider } from '../../providers/setting/setting';
import { AuthPage } from '../auth/auth';
import { UserService } from '../../shared/';
import { ClassConfig } from './class.config';

@IonicPage()
@Component({
  selector: 'page-class',
  templateUrl: 'class.html'
})
export class ClassPage extends AuthPage{

  menuItems : any

 
  header_title:any;
  constructor(public navCtrl: NavController, public navParams: NavParams, protected userService : UserService) {
    super(navCtrl,navParams,userService)
    this.header_title = SettingProvider.pageSettings.ClassPage.pageTitle
    this.menuItems = ClassConfig.menuItems;
  }

  goTo(pageName: string){
    this.navCtrl.push(pageName)
  }
}



